<template lang="html">

  <transition :name="'pop'" mode="out-in">

    <div class="back" v-if="show" @click="back()"><</div>

  </transition>


</template>

<script lang="js">

  export default  {

    name: 'back',

     data() {

      return {

        show: false

      }

    },


    mounted() {

      this.showActions();

    },


    methods: {

      back(){
        this.$router.go(-1);
      },


      showActions(){

        setInterval(() => {

          this.show = true;

        }, 400)

      }

    }


  }
</script>

<style scoped lang="scss">
  .back {

  }
</style>



