/*  DEPENDENCIES  */
import Vue from 'vue'
import VueRouter from 'vue-router';


/*  COMPONENTS */
import AppShell from './components/app-shell/index.vue'
import screensaver from './components/screensaver/index.vue'
import explore from './components/explore/index.vue'


/* USE */
Vue.use(VueRouter);



const routes = [
  { path: '/', name:'screensaver', component:  screensaver},
  { path: '/explore', name:'explore',  component: explore }
];


export const router = new VueRouter({
  routes
});




/*  INIT  */
const app = new Vue({

  router,

  components :{

    'app-shell' : AppShell

  },


  updated(){

    this.zoomIn();

  },

  mounted(){

    this.showScreensaver();

  },

  methods:{

    /* Zoom in/out */
    zoomIn(){

      let view = this.$route.name;
      let screensaver = document.getElementById("screensaver");

      //if not on the screensaver view, zoom in
      if(view != 'screensaver'){

        screensaver.classList.add('zoom');

      }else{

        screensaver.classList.remove('zoom');

      }

    },


    /* back to screensaver if inactive */
    showScreensaver(){

      // the timeout
      var mousetimeout;

      // lets reinstance 'this' so we ca use within 'document'
      let vue = this;



      // always clean the timeout as soon as the user touch the screen
      document.addEventListener("touchstart", function(e){

        clearTimeout(mousetimeout);

      });


      // set the timeout as soon as the user finishes touching the screen
      document.addEventListener("touchend", function(){

        mousetimeout = setTimeout(function(){

          // redirect to home
          vue.$router.push({ path: '/' });

        }, 5000); // 5 secs

      });


    }

  }

}).$mount('#app');



