import back from '../shared/back.vue';


export default  {

  name: 'navigation',

  props: [],

  components:{

    'back': back

  },

  data() {
    return {

    }
  },


  mounted() {

    this.zoomIn();

  },

  methods: {

    zoomIn(){
      let view = this.$route.name;
      let app = document.getElementById("screensaver");

      if(view != 'screensaver'){
        app.classList.add('zoom');
      }else{
        app.classList.remove('zoom');
      }
    }

  },

  computed: {

  }
}




